
.nodeName {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  display: block;
  overflow: hidden;
}
.panel-content {
  max-height: 50vh;
  min-height: 5vh;
  padding: 0px;
  overflow: hidden;
  overflow-y: scroll;
  font-family: Quicksand, sans-serif;
  font-weight: 600;
  font-size: 1.2em;
  color: #666;
}
.activityCard {
  max-height: 87vh;
  overflow: hidden;
  overflow-y: scroll;
}
.notificationBox {
  width: 100%;
  display: block;
  overflow: hidden;
  font-size: 20px;
  font-family: Quicksand, sans-serif !important;
  font-size: 1.6em;
  font-weight: 600;
  text-overflow: ellipsis;
}
.notificationBox-date {
  margin-top: 6px;
}
.notificationBox-humidity {
  border-left: 4px solid #e040fb !important;
  background-color: #E3C3E9 !important;
  padding: 4px;
}
.notificationBox-moisture {
  border-left: 4px solid #536dfe !important;
  background-color: #C9CEEB !important;
  padding: 4px;
}
.notificationBox-temperature {
  border-left: 4px solid #ff7043 !important;
  background-color: #FFD0C1 !important;
  padding: 4px;
}
.notificationBox-sunlight {
  border-left: 4px solid #ffc400 !important;
  background-color: #FFEDB8 !important;
  padding: 4px;
}
.avatar-humidity {
  background-color: #E3C3E9 !important;
}
.avatar-moisture {
  background-color: #C9CEEB !important;
}
.avatar-temperature {
  background-color: #FFD0C1 !important;
}
.avatar-sunlight {
  background-color: #FFEDB8 !important;
}
.expand-enter {
  max-height: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
  overflow: hidden;
}
.expand-enter-active {
  transition: all 2s cubic-bezier(0.97, 0.21, 0.84, 0.38);
  overflow: hidden;
}
.expand-enter-to {
  max-height: 2000px;
  overflow: hidden;
}
.expand-leave {
  max-height: 2000px;
  overflow: hidden;
}
.expand-leave-active {
  transition: all 0.5s cubic-bezier(0, 1, 0, 1);
  overflow: hidden;
}
.expand-leave-to {
  padding-top: 0px;
  padding-bottom: 0px;
  max-height: 0px;
  overflow: hidden;
}



